import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import ReactImageZoom from "react-image-zoom";
import Parking from "../../img/Parking.jpg";

const carouselTwoStyle={
    container:{
        margin:"2.8rem auto",
        backgroundColor:"black"
    },
    col:{
        margin:"4rem 1rem 4rem 1rem",
        padding:"0.9rem",
    },
    floorPlan:{
        margin:"3rem 2rem",
        textAlign:"center"
    },
    parkingDiv:{
        textAlign:"center"
    },
    parking:{
        width:"20rem",
        height:"20rem",
        margin:"3rem 2rem"
    }
}

function Carousel2(){

    const imgZoomProps = {
        width : 400,
        height: 400,
        zoomWidth: 400,
        scale:1.3,
        zoomLensStyle:{
            opacity:"0"
        },
        zoomPosition:"original",
        img:"https://i.pinimg.com/originals/9c/05/9f/9c059feef8c1ba25dba060caad25e1d3.jpg"

    };

    return(
        <Container style={carouselTwoStyle.container}>
            <Row>
                <Col style={carouselTwoStyle.col}>
                    <h2>Floor-Plan</h2>
                    <div style={carouselTwoStyle.floorPlan}>
                    <ReactImageZoom {...imgZoomProps} />
                    </div>
                </Col>
                <Col style={carouselTwoStyle.col}>
                    <h2>Parking</h2>
                    <div style={carouselTwoStyle.parkingDiv}>
                    <img style={carouselTwoStyle.parking} src={Parking} alt="Parking"/>
                    </div>
                </Col>
            </Row>
        </Container>
    );
}

export default Carousel2;