import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Overview from "./Overview";
import Button from "react-bootstrap/Button";

const carouselOneStyle={
    container:{
        margin:"2.8rem auto",
        backgroundColor:"black"
    },
    col:{
        margin:"2rem",
        padding:"0.9rem",
        marginLeft:"auto",
        marginRight:"auto"
    },
    img:{
        width:"30rem",
        height:"25rem"
    },
    button:{
        textAlign:"center",
        margin:"4rem"
    }
}

const link3d = "https://www.99acres.com/kanha-devki-nandan-manewada-nagpur-npxid-r320417#showModal";

function Carousel1(){
    return (
      <Container style={carouselOneStyle.container}>
      <Row>
      <Col md={4} lg={6} >
            <div style={carouselOneStyle.col}>
           <img style = {carouselOneStyle.img} src="https://projectcdn.99acres.com/project_data/e905b4/block2_54741/3D/29367_B2_1F1_3D.jpg" alt="Layout" />
           </div>
      </Col>
      <Col md={4} lg={6} style={carouselOneStyle.col}>
       <Overview/>
       <div style={carouselOneStyle.button}>
       <Button size="lg"  href={link3d} variant="light" active>3D-VIEW</Button>
       </div>
      </Col>
      </Row>
      </Container>
    );
}

export default Carousel1;